<template>
  <Loading :active="isLoading">
    <i class="fas fa-spinner fa-spin fa-5x"></i>
  </Loading>
  <div class="container-fluid p-0 top-spacing">
    <div>
      <button class="btn btn-success" @click="openModal(true)">
        <i class="fas fa-plus"></i> Êñ∞Â¢ûÁî¢ÂìÅ
      </button>
    </div>
  </div>
  <table class="table mt-4 text-center">
    <thead class="table-dark text-center">
      <tr>
        <th width="120" class="text-start">ÂàÜÈ°û</th>
        <th width="240" class="text-start">Áî¢ÂìÅÂêçÁ®±</th>
        <th width="100" class="text-end">ÂéüÂÉπ</th>
        <th width="100" class="text-end">ÂîÆÂÉπ</th>
        <th width="100">ÊòØÂê¶ÂïüÁî®</th>
        <th width="120">Á∑®ËºØ</th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="item in products" :key="item.id">
        <td class="text-start">{{ item.category }}</td>
        <td class="text-start">{{ item.title }}</td>
        <td class="text-right">
          <!-- Âä†ÂÖ•ÂçÉÂàÜËôü -->
          {{ $filters.currency(item.origin_price) }}
        </td>
        <td class="text-right">
          {{ $filters.currency(item.price) }}
        </td>
        <td class="text-center">
          <span class="text-success" v-if="item.is_enabled">ÂïüÁî®</span>
          <span class="text-muted" v-else>Êú™ÂïüÁî®</span>
        </td>
        <td class="text-center">
          <div class="btn-group">
            <button class="btn btn-outline-primary btn-sm" @click="openModal(false, item)">
              Á∑®ËºØ
            </button>
            <button class="btn btn-outline-danger btn-sm" @click="openDelProductModal(item)">
              Âà™Èô§
            </button>
          </div>
        </td>
      </tr>
    </tbody>
  </table>
  <Pagination :pages="pagination" @emit-pages="getProducts"></Pagination>
  <ProductModal
    ref="productModal"
    :product="tempProduct"
    @update-product="updateProduct"
  ></ProductModal>
</template>

<script>
import ProductModal from "@/components/backend/ProductModal.vue";
import Pagination from "@/components/Pagination.vue";
import Toast from "@/alert/Toast";

export default {
  data() {
    return {
      products: [],
      pagination: {},
      tempProduct: {},
      isNew: false,
      isLoading: false,
    };
  },
  components: {
    ProductModal,
    Pagination,
  },
  inject: ["emitter"],
  methods: {
    getProducts(page = 1) {
      const api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/products/?page=${page}`;
      this.isLoading = true;
      this.$http.get(api).then((res) => {
        this.isLoading = false;
        if (res.data.success) {
          this.products = res.data.products;
          this.pagination = res.data.pagination;
        }
      });
    },
    openModal(isNew, item) {
      if (isNew) {
        this.tempProduct = {};
      } else {
        this.tempProduct = { ...item };
      }
      this.isNew = isNew;
      const productComponent = this.$refs.productModal;
      productComponent.showModal();
    },
    updateProduct(item) {
      this.tempProduct = item;
      // Êñ∞Â¢û
      let api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/product`;
      let httpMethod = "post";
      // Á∑®ËºØ
      if (!this.isNew) {
        api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/product/${item.id}`;
        httpMethod = "put";
      }
      const productComponent = this.$refs.productModal;
      this.$http[httpMethod](api, { data: this.tempProduct }).then((response) => {
        productComponent.hideModal();
        if (response.data.success) {
          this.getProducts();
          Toast.fire({
            title: "Êõ¥Êñ∞ÊàêÂäü",
            icon: "success",
          });
        } else {
          // response.data.message ÈáçÊñ∞format
          const replacements = {
            title: "Ê®ôÈ°å",
            category: "ÂàÜÈ°û",
            unit: "ÂñÆ‰Ωç",
            origin_price: "ÂéüÂÉπ",
            price: "ÂîÆÂÉπ",
          };
          const replaceText = (messages) => {
            return messages.map((message) => {
              let updatedMessage = message;
              // Áõ¥Êé•ÊõøÊèõÊ∂àÊÅØ‰∏≠ÁöÑÈçµÂíåÂÄº
              for (const key in replacements) {
                if (updatedMessage.includes(key)) {
                  updatedMessage = updatedMessage.replace(new RegExp(key, "g"), replacements[key]);
                }
              }
              return updatedMessage; // ËøîÂõûÊõ¥Êñ∞ÂæåÁöÑÊ∂àÊÅØ
            });
          };
          let tempText = replaceText(response.data.message);
          let messagesTemp = [];
          tempText.forEach((item) => {
            item = item.trim();
            const [key, message] = item.split(" ");
            messagesTemp.push(`${key} ${message}`);
          });
          let result = messagesTemp.map((item) => `‚ö†Ô∏è${item}<br/>`);
          result = result.join("");
          Toast.fire({
            title: "üîîÊõ¥Êñ∞Â§±ÊïóÔºåË´ãÁ®çÂæåÂÜçË©¶üîî",
            // text: messagesTemp,
            html: `<b class="text-danger">${result}</b>`,
            icon: "error",
          });
        }
      });
    },
    // ÈñãÂïüÂà™Èô§ Modal
    openDelProductModal(item) {
      this.tempProduct = { ...item };
      this.$swal({
        title: "Á¢∫ÂÆöË¶ÅÂà™Èô§?",
        html: `ÊòØÂê¶Âà™Èô§<b class="text-danger">${this.tempProduct.title}</b>(Âà™Èô§ÂæåÂ∞áÁÑ°Ê≥ïÊÅ¢Âæ©)`,
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "ÊòØ, Á¢∫ÂÆöÂà™Èô§!",
        cancelButtonText: "ÂèñÊ∂à",
      }).then((result) => {
        if (result.isConfirmed) {
          this.delProduct();
          this.$swal("Â∑≤Âà™Èô§!", "Ê™îÊ°àÂ∑≤Ë¢´Âà™Èô§", "success");
        }
      });
    },
    delProduct() {
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/product/${this.tempProduct.id}`;
      this.$http.delete(url).then(() => {
        this.getProducts();
      });
    },
  },
  created() {
    this.getProducts();
    this.$emitter.emit("check-navbar", "products");
  },
};
</script>
